"use client";
import Link from "next/link";
import { useState, useEffect } from "react";
import { usePathname } from "next/navigation";
import {
	FaWhatsapp,
	FaBars,
	FaTimes,
	FaSearch,
	FaCartShopping,
	FaUser,
} from "react-icons/fa";
import {
	DropdownMenu,
	DropdownMenuContent,
	DropdownMenuItem,
	DropdownMenuLabel,
	DropdownMenuSeparator,
	DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu";
import { Button } from "@/components/ui/button";
import Image from "next/image";

export default function Header() {
	const [scrolled, setScrolled] = useState(false);
	const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);
	const pathname = usePathname();

	useEffect(() => {
		const handleScroll = () => {
			setScrolled(window.scrollY > 50);
		};

		window.addEventListener("scroll", handleScroll);
		return () => window.removeEventListener("scroll", handleScroll);
	}, []);

	const isServicesSection = () => {
		return pathname === "/services" || pathname?.includes("#services");
	};

	useEffect(() => {
		setIsMobileMenuOpen(false);
	}, [pathname]);

	return (
		<header
			className={`fixed top-0 left-0 right-0 bg-[#F1F1F1] shadow-md w-full z-50 ${
				scrolled ? "h-16" : "h-20"
			} transition-all duration-300`}
		>
			<div className="container mx-auto px-4 h-full">
				<div className="flex justify-between items-center h-full">
					{/* Menu Desktop */}
					<nav className="hidden lg:flex flex-1 justify-center">
						<div className="flex space-x-8 items-center">
							<Link
								href="/"
								className={`text-black text-lg font-medium hover:text-[#4F55B3] transition-colors ${
									pathname === "/" ? "underline" : ""
								}`}
							>
								INÍCIO
							</Link>
							<Link
								href="/#escritorio"
								className={`text-black text-lg font-medium hover:text-[#4F55B3] transition-colors ${
									pathname === "/about" ? "underline" : ""
								}`}
							>
								O ESCRITÓRIO
							</Link>
							<Link
								href="/#services"
								className={`text-black text-lg font-medium hover:text-[#4F55B3] transition-colors ${
									isServicesSection() ? "underline" : ""
								}`}
							>
								NOSSOS SERVIÇOS
							</Link>
							<Link
								href="/equipe"
								className={`text-black text-lg font-medium hover:text-[#4F55B3] transition-colors ${
									pathname === "/equipe" ? "underline" : ""
								}`}
							>
								EQUIPE
							</Link>
							<Link
								href="/blog"
								className={`text-black text-lg font-medium hover:text-[#4F55B3] transition-colors ${
									pathname === "/blog" ? "underline" : ""
								}`}
							>
								BLOG
							</Link>
						</div>
					</nav>

					{/* Botão Menu Mobile */}
					<button
						className="lg:hidden text-2xl text-gray-800"
						onClick={() => setIsMobileMenuOpen(!isMobileMenuOpen)}
					>
						{isMobileMenuOpen ? <FaTimes /> : <FaBars />}
					</button>
				</div>

				{/* Menu Mobile */}
				{isMobileMenuOpen && (
					<div className="lg:hidden bg-[#F1F1F1] absolute left-0 right-0 top-full shadow-lg py-4 px-6 z-50">
						<div className="flex flex-col space-y-4">
							<Link
								href="/"
								className={`text-black text-lg font-medium hover:text-[#4F55B3] transition-colors ${
									pathname === "/" ? "underline" : ""
								}`}
							>
								INÍCIO
							</Link>
							<Link
								href="/#escritorio"
								className={`text-black text-lg font-medium hover:text-[#4F55B3] transition-colors ${
									pathname === "/about" ? "underline" : ""
								}`}
							>
								O ESCRITÓRIO
							</Link>
							<Link
								href="/#services"
								className={`text-black text-lg font-medium hover:text-[#4F55B3] transition-colors ${
									isServicesSection() ? "underline" : ""
								}`}
							>
								NOSSOS SERVIÇOS
							</Link>
							<Link
								href="/equipe"
								className={`text-black text-lg font-medium hover:text-[#4F55B3] transition-colors ${
									pathname === "/equipe" ? "underline" : ""
								}`}
							>
								EQUIPE
							</Link>
							<Link
								href="/blog"
								className={`text-black text-lg font-medium hover:text-[#4F55B3] transition-colors ${
									pathname === "/blog" ? "underline" : ""
								}`}
							>
								BLOG
							</Link>
							<Link
								href="https://wa.me/seunumerodetelefone"
								target="_blank"
								rel="noopener noreferrer"
								className="mt-4"
							></Link>
						</div>
					</div>
				)}
			</div>
		</header>
	);
}
